$bth-width: unset;
$bth-height: 40px;
$btn-border-width: 1px;
$btn-border-color: rgba(#6772e5, 1);
$bth-border-radius: 6px;
$bth-box-shadow: none;
$bth-background-color: rgba(#6772e5, 1);
$bth-color: rgba(#fff, 1);
$bth-font-size: 14px;
$bth-font-weight: bold;

$btn-color--border: rgba(#000, 1);

// ==================================================
// ==================================================
// ==================================================

.c-btn {
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
	min-width: 110px;
  //max-width: $bth-width;
  height: $bth-height;
  border: $btn-border-width solid $btn-border-color;
  box-shadow: $bth-box-shadow;
  border-radius: $bth-border-radius;
  color: $bth-color;
  font-size: $bth-font-size;
  font-weight: $bth-font-weight;
	letter-spacing: 0.4px;

  &--border {
    background-color: transparent;
    color: $btn-color--border;
  }

  &--bg {
    background-color: $bth-background-color;

		&:hover {
			color: #ffffff;
		}
  }
}

// BUTTON :: END
